.qrcode-modal {
    h3 {
        p {
            user-select: initial;
            margin-bottom: 6px;
        }
        &:nth-child(1) {
            margin-bottom: 20px;
            color: #1a9bec;
        }
        &:nth-child(3) {
            margin: 20px;
            color: #686868;
            font-weight: normal;
        }
    }
}