.wifi-title {
    margin-bottom: 20px;
}

.wrapper-wifibox {
    width: 100%;
    .wifibox-item {
        width: 100%;
        text-align: left;
        td {
            text-align: right;
            padding: 10px 0;
        }
    }

    input[type=text], input[type=password] {
        width: 100%;
        font-size: 18px;
        outline: none;
        padding: 4px;
    }
    
    .password:hover {
        .btn-eye {
            display: inline;
        }
    }
    
    .password input {
        padding-right: 24px;
    }
    
    .btn-eye {
        cursor: pointer;
        width: 16px;
        margin-left: -16px;
        display: none;
        transform: translateX(-8px);
    }
}

.wifi-modal {
    background-color: #F9F9F9;

    .swal-button {
        transition: all 0.1s ease;
    }

    .swal-button--confirm {
        background-color: orange;
        color: white;
        &:hover {
            background-color: orange;
            opacity: 0.85;
        }
    }

    .swal-button--cancel {
        background-color: #78cbf2;
        color: white;
        &:hover {
            background-color: #78cbf2;
            opacity: 0.85;
        }
    }
}