.login--form {
    .logo {
        width: 100px;
    }
    h3 {
        margin-bottom: 40px;
    }
    .item {
        margin: 10px 0;
        text-align: left;
        >span {
            display: block;
            text-align: left;
            margin-bottom: 10px;
        }
        input {
            display: block;
            width: 100%;
            padding: 4px;
        }
        .link {
            font-size: 12px;
            cursor: pointer;
            color: #30A9DE;
            p {
                margin-top: 6px;
            }
            &:hover {
                color: #1776a0;
                text-decoration: underline;
            }
        }
    }
    .controller {
        padding-top: 10px;
        text-align: right;
        button {
            margin-left: 16px;
            border-radius: 4px;
            font-size: 18px;
            background-color: #30A9DE;
            color: #FFF;
            padding: 8px 20px;
            cursor: pointer;
            font-family: Verdana, 'luximb', 'Cascadia Code', 'Source Code Pro';
            &:hover {
                opacity: 0.8;
            }
        }
        button.loading {
            cursor: not-allowed;
            opacity: 0.6;
        }
        button.btn-cancel {
            background-color: orange;
        }
        .iconfont.spin {
            display: inline-block;
            margin-right: 6px;
            animation: spin 1s infinite ease;
        }
    }
}
