.header-form {
    padding: 0 10px;
    .btn {
        padding: 0 10px;
        display: inline-block;
        float: right;
        height: 30px;
        line-height: 30px;
        margin: 10px 6px;
        cursor: pointer;

        i, span {
            font-size: 13px;
        }

        i {
            margin-right: 6px;
        }

        &:hover {
            opacity: 0.7;
        }
    }
}

.binding-tip-modal {
    width: 485px;
    .swal-footer {
        height: 70px;
        .swal-button-container {
            float: right;
        }
    }
}