.config-form {
    width: 480px;
    margin: 50px auto 0 auto;
    background-color: #eee;
    padding: 10px 30px;

    h3 {
        font-size: 24px;
        text-align: center;
    }

    table {
        margin: 12px 0 20px 0;
        td {
            &.key {
                text-align: right;
                vertical-align: sub;
            }
            padding: 4px 4px;

            .copy {
                width: 16px;
                margin-left: 6px;
                cursor: pointer;
            }

            .text-copy {
                font-size: 14px;
                color: #0DB8FC;
                margin-left: 6px;
            }

            .inputbox {
                width: 250px;
                height: 16px;
                clear: both;
                background-color: skyblue;

                span, input {
                    display: block;
                    float: left;
                    outline: none;
                }

                span {
                    width: 64px;
                    text-align: right;
                    font-size: 12px;
                    background-color: #0DB8FC;
                    color: #efefef;
                    padding: 0 3px;
                }

                input {
                    width: 180px;
                    border: none;
                    margin: 0;
                    font-size: 12px;
                    color: #333;
                    font-weight: bolder;
                    background-color: transparent;
                }

                &.password:hover {
                    .btn-eye {
                        display: inline;
                    }
                }
            
                & input {
                    padding-right: 4px;
                }
            
                .btn-eye {
                    cursor: pointer;
                    width: 16px;
                    margin-left: -22px;
                    display: none;
                }
            }

            button {
                width: 100%;
                cursor: pointer;
                text-align: center;
                font-size: 14px;
                padding: 6px;
                background-color: #0DB8FC;
                color: #efefef;
                margin-top: 6px;
            }
        }
    }

    .footer {
        text-align: right;

        button {
            font-size: 16px;
            padding: 4px 12px;
            margin-left: 20px;
            cursor: pointer;
            color: #FFFFFF;
            border-radius: 2px;

            &.btn-save {
                background-color: #1E90FF;
            }

            &.btn-close {
                background-color: #FF9900;
            }
        }
    }
}