.list-device-wrapper {
    position: relative;
    .top-arrow, .bottom-arrow {
        position: absolute;
        left: 0;
        right: 0;
        height: 40px;
        background-color: rgba(0, 0, 0, 0.16);
        z-index: 2;
        transition: all .2s ease;
        &.show {
            background-color: rgba(41, 153, 228, 0.78);
        }
    }
    .top-arrow {
        top: 0;
        img {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
        }
    }
    .bottom-arrow {
        bottom: 0;
        img {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
        }
    }
}

.list-device {
    width: 180px;
    height: 100%;
    padding: 40px 0;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    &::-webkit-scrollbar {
        display: none;
    }
}

.item-device {
    flex: 1;
    text-align: center;
    cursor: pointer;
    opacity: 0.8;
    transition: all .2s ease;
    display: flex;
    flex-direction: column;

    figure {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        user-select: none;
        flex: 1;
        padding: 16px 0;
    }

    .avatar {
        width: 90px;
        filter: grayscale(1);
        transition: all .2s ease;
    }

    .own-count {
        position: absolute;
        right: 10px;
        bottom: 10px;
    }

    figcaption {
        margin-top: 8px;
        font-size: 14px;
        font-family: 'Cascadia Code';
    }

    &.selected {
        opacity: 1;
    }

    &.selected .avatar, &:hover .avatar{
        filter: grayscale(0);
    }
}