.timercfg-modal {
    width: 600px;
}

.wrapper-timercambox {
    width: 100%;
    .wifibox-item {
        width: 100%;
        text-align: left;
        td {
            padding: 10px 0;
            &:first-child {
                text-align: right;
            }
        }
        td.field-name {
            width: 160px;
        }
    }

    input[type=text], input[type=password], input[type=number], select {
        width: 100%;
        font-size: 18px;
        outline: none;
        padding: 4px;
    }

    input[type=number] {
        width: 120px;
        margin-right: 10px;
    }
    
    .password:hover {
        .btn-eye {
            display: inline;
        }
    }
    
    .password input {
        padding-right: 24px;
    }
    
    .btn-eye {
        cursor: pointer;
        width: 16px;
        margin-left: -16px;
        display: none;
        transform: translateX(-8px);
    }
}

.timercfg-modal {
    background-color: #F9F9F9;

    .swal-button {
        transition: all 0.1s ease;
    }

    .swal-button--confirm {
        background-color: orange;
        color: white;
        &:hover {
            background-color: orange;
            opacity: 0.85;
        }
    }

    .swal-button--cancel {
        background-color: #78cbf2;
        color: white;
        &:hover {
            background-color: #78cbf2;
            opacity: 0.85;
        }
    }

    .swal-button--config {
        background-color: #3ac569;
        color: white;
        &:hover {
            background-color: #3ac569;
            opacity: 0.85;
        }
    }
}