.app-top-bar {
    $header-height: 40px;
    $header-font-size: 20px;

    width: 100%;
    height: $header-height;
    padding-left: 6px;
    -webkit-app-region: drag;

    .logo {
        width: 200px;
        height: 100%;
        float: left;
        line-height: $header-height;
        span {
            font-size: $header-font-size;
        }
    }

    .control-box {
        height: 100%;
        float: right;
        text-align: right;
        line-height: $header-height;
        -webkit-app-region: no-drag;
        .btn-control {
            height: 100%;
            cursor: pointer;
            transition: all .15s ease;
            position: relative;
            i {
                padding: 0 18px;
                font-size: $header-font-size;
            }
            &.notice::after {
                content: '';
                position: absolute;
                top: 8px;
                right: 14px;
                width: 6px;
                height: 6px;
                border-radius: 6px;
                background-color: red;
            }
            span {
                color: #FFF;
                padding-right: 18px;
            }
        }
    }
}

.btn-control {
    .menu {
        display: none;
        position: absolute;
        top: 40px;
        width: 100%;
        background-color: #FFF;
        p {
            font-size: 14px;
            padding: 8px 0;
            i, span {
                color: #333 !important;
            }
        }
    }
    &:hover .menu {
        display: block;
        &:hover {
            background-color: #5eaeff;
            i, span {
                color: #FFF !important;
            }
        }
    }
}