.wrapper-item-firmware {
    width: 33.333333%;

    article {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 90%;
        height: 540px;
        margin: 16px auto;
        border: 1px solid lightgray;
        box-shadow: 1px 1px 3px lightgray;
        transition: all .2s ease;

        &:hover {
            box-shadow: 3px 3px 3px lightgray;
        }

        .cover {
            display: block;
            height: 280px;
            object-fit: cover;
        }

        h2 {
            font-size: 16px;
            border-bottom: 1px solid lightgray;

            .name {
                display: inline-block;
                max-width: 190px;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            padding: 4px 8px;

            .tag {
                font-size: 10px;
                margin: 0 8px;
                padding: 4px 8px;
                border-radius: 6px;
            }

            .selector-version {
                float: right;
                outline: none;
                margin-top: 2px;
                background-color: transparent;
                max-width: 120px;
            }
            a.link {
                color: #aaa;
                transition: all .15s ease;
                &:hover {
                    color: #333;
                }
            }
            i.github {
                float: right;
                line-height: 22px;
                margin-right: 8px;
                cursor: pointer;
            }
        }

        p {
            padding: 4px 8px;
            font-size: 13px;
            flex: 1;
            background-position: 50% 50%;
            background-size: 50%;
            background-repeat: no-repeat;
            transition: all .15s ease;
            user-select: initial;
            word-break: break-all;
            overflow : hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 10;
            -webkit-box-orient: vertical;
            max-height: 99999px;
            
            &:hover {
                background-size: 75%;
            }
        }

        .footer {
            text-align: right;
            padding: 0 12px;
            margin-bottom: 6px;
            height: 30px;
            line-height: 30px;

            .more {
                font-size: 12px;
                float: left;
                cursor: pointer;
                color: #0099CC;
            }

            button {
                position: relative;
                border: 1px solid #333;
                padding: 4px 10px;
                cursor: pointer;
                transition: all .25s ease;
                overflow: hidden;

                i, span {
                    position: relative;
                    z-index: 2;
                    transition: all .25s ease;
                }

                &.burn i {
                    color: #FF0033;
                }

                span {
                    margin-left: 6px;
                }

                .mask, .progress {
                    position: absolute;
                    top: 0;
                    left: 0;
                    margin: auto;
                    z-index: 1;
                }
 
                .progress {
                    width: 0;
                    height: 100%;
                    background-color: #00A950;
                }

                &.burn .mask, &.remove .mask {
                    width: 0;
                    height: 100%;
                    background-color: #FF0033;
                }

                &.remove {
                    margin-right: 12px;
                }

                &.remove .mask {
                    background-color: orange;
                }

                &.download .mask {
                    width: 100%;
                    height: 0;
                    background-color: rgb(42, 136, 230);
                }

                &.info {
                    margin-right: 12px;
                }

                &.info .mask {
                    width: 100%;
                    height: 0;
                    background-color: rgb(184, 77, 255);
                }

                &.downloading .mask {
                    display: none;
                }

                &:hover {
                    border-color: transparent;

                    i, span {
                        color: #FFFFFF;
                    }

                    &.burn .mask, &.remove .mask {
                        animation: fill-horizontal .3s ease forwards;
                    }

                    &.download .mask, &.info .mask {
                        animation: fill-vertical .3s ease forwards;
                    }
                }

                &.downloading:hover {
                    border-color: #333;

                    i, span {
                        color: #333;
                    }
                }
            }
        }

        .remark {
            height: 24px;
            font-size: 12px;
            line-height: 24px;
            color: #a1a1a1;
            text-align: right;
            .left {
                float: left;
                margin-left: 6px;
            }
            .right {
                float: right;
                margin-right: 16px;
                i {
                    font-size: 12px;
                }
            }
            .clear-fix {
                clear: both;
            }
            i {
                margin-right: 4px;
            }
        }
    }
}

.fake-out {
    animation: fakeOut .35s forwards;
}

@keyframes fakeOut {
    from {
        transform: translateY(-30px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}